/* App */

.main-title {
  padding: 17px;
  font-size: 18px;
  color: #65637d;
  line-height: normal;
  margin: 0;
  margin-top: -28px;
  margin-bottom: 0px;
  /*text-decoration-line: underline;
  text-decoration-style: dotted;*/
}

.cards {
  display: grid;
  /*margin: 1.5em 3em;*/
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-flow: row dense;
  grid-gap: 2em;
  grid-auto-rows: 300px;    /* grid-auto-rows: 124px; // altura si se quita la imagen de los items del home*/
  margin-right: 20px;
  margin-left: 30px;
  margin-top: 0px;	
}

.card {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  /* height: fit-content; */ /*se agrega si se quiere ocultar la imagen de los items del home*/
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -webkit-box-shadow: -3px 3px 10px 0px rgba(168, 168, 168, 0.4);
          box-shadow: -3px 3px 10px 0px rgba(168, 168, 168, 0.4);
  text-align: center;
  background: #ddd;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  margin-bottom: -1px;
  /*  height: 89px; */ /* para no mostrar imagen en recipe page*/
}

.card:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.card:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.25;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.card:hover:after {
  opacity: 0;
}

.card-text {
  /*margin-bottom: -10px; */
  position: relative;
  z-index: 1;
  color: rgb(39, 37, 37);
  background: whitesmoke;
  letter-spacing: 1px;
  padding: 12px;
}

.card-text a {
  text-decoration: none;
}

.card-text h4 {
  margin: 0;
  padding: 0.2em;
  margin-bottom: -12px;
}

