/* Recipe Page */

.recipe-image {
     -webkit-margin-top-collapse: discard;
     height: 60vh; /* se comenta para ocultar imagen en Recipe/RecipePage.js */ /*se descomenta agosto 9 2021*/
     max-width: 100%;
}

.recipe-image:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 60vh;
  opacity: 0.25;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.recipe {
  text-align: center;
  background: #f8f8f8;
  padding: 1em;
}

.recipe h1 {
  font-size: 2.5rem;
  text-align: center;
}

.recipe-header {
  overflow: hidden;
}

.recipe-header > p {
  margin: 0;
}

.recipe-description {
  font-size: 2rem;
  color: darkslategray;
  padding: 8px;
  font-weight: 200;
  background: white;
  -webkit-box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  font-style: italic;
}

.recipe-name {
  color: #000000;
  text-decoration: none;
  /*text-decoration-style: wavy;*/
}

.recipe-instructions {
  text-align: left;
  padding: 0 2em;
  width: 860px;
  margin: auto;	
}

@media only screen and (max-width: 600px) {
  .recipe-instructions {
      width: 89%;
   }
}

